<template>
  <div id="background" class="main-content">
    <div v-if="loading" class="loading"></div>

    <div v-else class="app flex-row align-items-center">
      <div class="container">
        <b-row class="justify-content-center">
          <b-col md="7">
            <div class="text-center mb-4">
              <img
                class="navbar-brand-full p-3"
                src="@/assets/logo-w.svg"
                width="38%"
                alt="Expense Reduction Management Logo"
              />
            </div>
            <b-card-group>
              <b-card no-body class="p-4">
                <b-card-body class="card-padding-top">
                  <b-row v-if="showMsg">
                    <b-button
                      @click="backTo"
                      class="back-btn"
                      variant="primary"
                      >{{ $t("CLOSE") }}</b-button
                    >
                  </b-row>

                  <b-row>
                    <b-col v-if="showMsg" cols="12">
                      <div class="w-full d-flex justify-content-center">
                        <i
                          class="fa fa-check-circle-o fa-4x"
                          aria-hidden="true"
                          style="color: green; font-size: 80px"
                        ></i>
                      </div>
                      <div class="mb-3 font-weight-bold">
                        {{ $t("RESETPASSWORDSUCCESS") }}
                      </div>
                      <div class="font-weight-bold">
                        {{ $t("LOGINID") }}：
                        <span>{{ companyInfo.login_id }}</span>
                      </div>
                      <div class="mb-4 font-weight-bold">
                        {{ $t("LOGINPASSWORD") }}：
                        <span>{{ password }}</span>
                      </div>

                      <div class="font-weight-bold">
                        {{ $t("RESETCOMPLETE1") }}
                        <span class="text-blue">
                          {{ companyInfo.mail_address }}
                        </span>
                        {{ $t("PASSWORDRESETLINKSENT2") }}
                        <div>{{ $t("RESETCOMPLETE2") }}</div>
                      </div>
                    </b-col>
                  </b-row>

                  <b-form @submit.prevent="submit" v-if="!showMsg">
                    <div class="text-center title-fw title-fs">
                      {{ $t("RESETPASSWORD") }}
                    </div>
                    <br />
                    <p class="text-center text-danger">
                      {{ validationMessage }}
                    </p>

                    <b-row class="mb-2">
                      <b-col>
                        <div v-if="access_role == 'USER'" class="title-text">
                          {{ $t("CUSTOMERINFORMATION") }}
                        </div>
                        <div v-else class="title-text">
                          {{ $t("PERSONINCHARGEINFO") }}
                        </div>
                      </b-col>
                    </b-row>
                    <div class="padding-level-1">
                      <b-row class="mb-2">
                        <b-col v-if="access_role == 'USER'" cols="5">{{
                          $t("COMPANYNAME")
                        }}</b-col>
                        <b-col v-else cols="5">{{
                          $t("AFFILIATIONNAME")
                        }}</b-col>
                        <b-col cols="7">{{ companyInfo.company_name }}</b-col>
                      </b-row>

                      <b-row class="mb-2">
                        <b-col cols="5">{{ $t("CONTACTNAME") }}</b-col>
                        <b-col cols="7">{{ companyInfo.contact_name }}</b-col>
                      </b-row>

                      <b-row class="mb-2">
                        <b-col cols="5">{{ $t("MAILADDRESS") }}</b-col>
                        <b-col cols="7">{{ companyInfo.mail_address }}</b-col>
                      </b-row>
                    </div>
                    <b-row class="mb-2 mt-3">
                      <b-col>
                        <div class="title-text">
                          {{ $t("LOGININFORMATION") }}
                        </div>
                      </b-col>
                    </b-row>
                    <div class="padding-level-1">
                      <b-row class="mb-2">
                        <b-col cols="5">{{ $t("LOGINID") }}</b-col>
                        <b-col cols="7">{{ companyInfo.login_id }}</b-col>
                      </b-row>
                      <b-form-group>
                        <b-row>
                          <b-col cols="12" sm="6" lg="5">
                            <label class="line-3">{{
                              $t("NEWPASSWORD")
                            }}</label>
                            <b-button
                              variant="primary"
                              class="px-4 required-btn ml-1"
                              >{{ $t("REQUIREDBUTTON") }}</b-button
                            >
                          </b-col>

                          <b-col cols="12" sm="6" md="7">
                            <b-input-group class="mb-1">
                              <template v-slot:append>
                                <b-input-group-text
                                  @click="toggleNewPassword"
                                  class="pointer"
                                >
                                  <strong>
                                    <span
                                      :class="{
                                        'view-password-selected': showNewPassword,
                                      }"
                                      toggle="#password-field"
                                      class="fa fa-fw fa-eye toggle-password pointer"
                                    ></span>
                                  </strong>
                                </b-input-group-text>
                              </template>
                              <b-form-input
                                v-model.trim="$v.password.$model"
                                :placeholder="$t('NEWPASSWORD')"
                                :type="newPasswordType"
                                class="form-control"
                              />
                            </b-input-group>
                            <show-errors
                              :control="$v.password"
                              :field="$t('NEWPASSWORD')"
                            />
                          </b-col>
                        </b-row>
                      </b-form-group>
                      <b-form-group>
                        <b-row>
                          <b-col cols="12" sm="6" lg="5">
                            <label class="line-3">
                              {{ $t("CONFIRMPASSWORD") }}
                            </label>
                            <b-button
                              variant="primary"
                              class="px-4 required-btn ml-1"
                              >{{ $t("REQUIREDBUTTON") }}</b-button
                            >
                          </b-col>
                          <b-col cols="12" sm="6" md="7">
                            <b-input-group class="mb-1">
                              <template v-slot:append>
                                <b-input-group-text
                                  @click="toggleConfirmPassword"
                                  class="pointer"
                                >
                                  <strong>
                                    <span
                                      :class="{
                                        'view-password-selected': showConfirmPassword,
                                      }"
                                      toggle="#password-field"
                                      class="fa fa-fw fa-eye toggle-password"
                                    ></span>
                                  </strong>
                                </b-input-group-text>
                              </template>
                              <b-form-input
                                v-model.trim="$v.confirm_password.$model"
                                :placeholder="$t('CONFIRMPASSWORD')"
                                :type="confirmPasswordType"
                                class="form-control"
                              />
                            </b-input-group>
                            <show-errors
                              :control="$v.confirm_password"
                              :field="$t('CONFIRMPASSWORD')"
                            />
                          </b-col>
                        </b-row>
                      </b-form-group>
                      <b-row class="mb-4">
                        <b-col>
                          <div class="small-fs mb-2 text-center">
                            {{ $t("RESETPASSWORDLEBEL2") }}
                          </div>
                        </b-col>
                      </b-row>
                    </div>

                    <b-row>
                      <b-col cols="12" class="text-center">
                        <b-button
                          :disabled="onSubmit"
                          variant="primary"
                          type="submit"
                          class="px-4 reset-btn"
                        >
                          {{ $t("RESET") }}
                          <i v-if="onSubmit" class="fa fa-spinner fa-spin" />
                        </b-button>
                      </b-col>
                    </b-row>
                  </b-form>
                </b-card-body>
              </b-card>
            </b-card-group>
          </b-col>
        </b-row>
      </div>
    </div>
  </div>
</template>

<script>
import {
  required,
  sameAs,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import i18n from "@/plugins/i18n";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "UserResetPassword",
  components: { ShowErrors },
  data() {
    return {
      loading: false,
      showMsg: false,
      password: "",
      confirm_password: "",
      validationMessage: "",
      onSubmit: false,
      showNewPassword: false,
      showConfirmPassword: false,
      companyInfo: {
        company_name: "",
        contact_name: "",
        mail_address: "",
        login_id: "",
        client_account_no: "",
      },
    };
  },
  validations: {
    password: { required, minLength: minLength(8), maxLength: maxLength(15) },
    confirm_password: { sameAsPassword: sameAs("password") },
  },
  computed: {
    newPasswordType() {
      return this.showNewPassword ? "text" : "password";
    },
    confirmPasswordType() {
      return this.showConfirmPassword ? "text" : "password";
    },
  },
  watch: {
    password() {
      this.clearApiValidation();
    },
    confirm_password() {
      this.clearApiValidation();
    },
    // call again the method if the route changes
    $route: "verifyToken",
  },
  created() {
    this.verifyToken();
  },
  beforeCreate() {
    if (window.location.pathname.includes("mng")) {
      localStorage.setItem("role", "ADMIN");
      this.access_role = "ADMIN";
    } else {
      localStorage.setItem("role", "USER");
      this.access_role = "USER";
    }
  },
  methods: {
    toggleOnSubmit() {
      this.onSubmit = !this.onSubmit;
    },
    async submit() {
      this.$v.$touch();
      /* If valid it will navigate to success screen */
      if (!this.$v.$invalid) {
        this.toggleOnSubmit();
        if (localStorage.getItem("role") == "USER") {
          const { data } = await BasicInfoApis.postAPI(endPoint.RESETPASSWORD, {
            remind_mail: this.companyInfo.mail_address,
            client_account_no: this.companyInfo.client_account_no,
            client_login_pass: this.password,
            client_name: this.companyInfo.contact_name,
            client_login_id: this.companyInfo.login_id,
          });

          if (data.success) {
            let successHtml = this.getSuccessHtml();
            this.$swal({
              width: 750,
              type: "success",
              html: successHtml,
              customClass: {
                popup: "format-pre",
              },
            }).then(() => {
              this.$router.push({ name: "user_login" });
            });
          } else {
            this.validationMessage = this.$t("backend." + data.message.code);
          }
        } else {
          const { data } = await BasicInfoApis.postAPI(
            endPoint.MRESETPASSWORD,
            {
              remind_mail: this.companyInfo.mail_address,
              client_account_no: this.companyInfo.client_account_no,
              client_login_pass: this.password,
              client_name: this.companyInfo.contact_name,
              client_login_id: this.companyInfo.login_id,
            }
          );

          if (data.success) {
            let successHtml = this.getSuccessHtml();
            this.$swal({
              width: 750,
              type: "success",
              html: successHtml,
              customClass: {
                popup: "format-pre",
              },
            }).then(() => {
              this.$router.push({ name: "admin_login" });
            });
          } else {
            this.validationMessage = this.$t("backend." + data.message.code);
          }
        }
        this.toggleOnSubmit();
      }
    },

    async verifyToken() {
      this.loading = true;

      if (localStorage.getItem("role") == "USER") {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.VERIFYRESETPASSWORDTOKEN,
          {
            onetime_pass: this.$route.params.token,
          }
        );
        this.loading = false;
        if (data.success) {
          let userData = data.data;
          this.companyInfo.company_name = userData.client_name;
          this.companyInfo.contact_name = userData.client_person_name;
          this.companyInfo.mail_address = userData.client_mail;
          this.companyInfo.client_account_no = userData.client_account_no;
          this.companyInfo.login_id = userData.client_login_id;
        } else {
          this.$router.push("/404");
        }
      } else {
        const { data } = await BasicInfoApis.postAPI(
          endPoint.MVERIFYRESETPASSWORDTOKEN,
          {
            onetime_pass: this.$route.params.token,
          }
        );
        this.loading = false;
        if (data.success) {
          let userData = data.data;
          this.companyInfo.company_name = userData.client_name;
          this.companyInfo.contact_name = userData.client_person_name;
          this.companyInfo.mail_address = userData.client_mail;
          this.companyInfo.client_account_no = userData.client_account_no;
          this.companyInfo.login_id = userData.client_login_id;
        } else {
          this.$router.push("/404");
        }
      }
    },
    backTo() {
      if (localStorage.getItem("role") == "ADMIN") {
        this.$router.push({ name: "admin_login" });
      } else {
        this.$router.push("/");
      }
    },
    toggleNewPassword() {
      this.showNewPassword = !this.showNewPassword;
    },
    toggleConfirmPassword() {
      this.showConfirmPassword = !this.showConfirmPassword;
    },
    getSuccessHtml() {
      let successHtml =
        `<div class="mb-3 font-weight-bold text-left"> ` +
        i18n.t("RESETPASSWORDSUCCESS") +
        ` </div>`;
      successHtml +=
        `<div class="font-weight-bold text-left">` +
        i18n.t("LOGINID") +
        `：<span>` +
        this.companyInfo.login_id +
        `</span></div>`;
      successHtml +=
        `<div class="mb-4 font-weight-bold text-left">` +
        i18n.t("LOGINPASSWORD") +
        `：<span>` +
        this.password +
        `</span>  </div>`;
      successHtml +=
        `<div class="font-weight-bold text-left">` +
        i18n.t("RESETCOMPLETE1") +
        `<span class="text-blue">` +
        this.companyInfo.mail_address +
        `</span>` +
        i18n.t("PASSWORDRESETLINKSENT2") +
        `<div>` +
        i18n.t("RESETCOMPLETE2") +
        `</div></div>`;
      return successHtml;
    },
    clearApiValidation() {
      this.validationMessage = "";
    },
  },
};
</script>
<style scoped lang="scss">
.title-text {
  font-size: 18px;
  font-weight: 800;
}
.view-password-selected {
  color: blue;
}
.line-3 {
  line-height: 2.4;
}
.p-top-5 {
  padding: 0.31rem 0rem !important;
}
.reset-btn {
  padding: 10px 50px 10px 50px !important;
  border-radius: 7px;
  width: 220px;
}
.card-padding-top {
  padding-top: 0rem;
}
</style>
